import { useState } from "react";
import Child from "./Child";

function Parent() {
  const [name, setName] = useState("");
  function showName(data) {
    name === "" ? setName(data) : setName("");
  }
  return (
    <>
      <p>hello i am parent comp</p>
      <h3>{name}</h3>
      <Child showName={showName} />
    </>
  );
}

export default Parent;
